body{
  width: 80%;
  border: 1px solid blue;
  margin: 0% 10%;
}

div{
  border: 1px solid blue;
  padding: auto 5px;
}
img{
  max-height: 100px;
  max-width: 100px;
}


#idBtn{
  color: rgb(97, 96, 96);
  border: none;
  border-radius: 4px;
  margin: 5px;
  font-family: inherit;
}
#idBtn:hover{
  color: rgb(228, 224, 224);

  background-image: linear-gradient(180deg, rgba(0, 0, 255, 0.685), rgba(8, 8, 226, 0.829),rgba(8, 8, 138, 0.767));

}
.idBtnFocus{
  background-image: linear-gradient(180deg, rgba(0, 0, 255, 0.685)rgba(8, 8, 138, 0.767), rgba(8, 8, 226, 0.829),rgba(0, 0, 255, 0.685));
}


.warning{
  background-color: rgba(255, 0, 0, 0.589);
  color: white;
  padding: 5px;
  text-align: center;
}
textarea{
  width: 400px;
  height: 100px;
  color:#474d58;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding: 5px;
  margin: auto;
  display: block;

}
input{
  width: 450px;
  color:#474d58;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding: 5px;
  margin: 5px auto;
  display: block;
  background-color: rgb(247, 250, 252);
  border: 1px solid rgb(68, 60, 60);
}
input:focus{
  outline: none;
  border: 0px;
  border-bottom:  1px solid blue;
}
select{
  color: rgb(100, 97, 97);
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  padding: 7px;
  margin: 10px auto;
}

fieldset{
  margin: 10px 10%;

}
.cipher{
  margin: auto 50%;
  padding: 5px;
}

form{
  margin: 10px auto;
}

h1{
  color:blue;
  font-style: italic;
}

.note{
  color: grey;
  padding-top : 5px;
  font-size: 15px;
  list-style-type: none;
}
.msg{
  color: red;
  background: lightgrey;
  font-size: 20px;
  border: 5px solid rgb(75, 66, 66);
  padding: 10px;
  margin-bottom: 10px;
}
